<template>
        <div>
            <label class="b-24-black" v-html="t('profile.Login')"></label>
        </div>
        <form class="py-5" @submit.prevent="handleLogin">
                <div class="mb-3 px-3 text-start">
                        <label for="disabledTextInput" class="form-label b-16-gray"
                                v-html="t('profile.usuario')"></label>
                        <input type="text" class="form-control py-3" v-model="username"
                                :placeholder="t('profile.usuario')">
                </div>
                <div class="mb-3 px-3 text-start">
                        <label for="disabledTextInput" class="form-label b-16-gray"
                                v-html="t('profile.contraseña')"></label>
                        <input type="password" v-model="password" class="form-control py-3" value="password" id="pass"
                                :placeholder="t('profile.contraseña')">
                        <input class="mt-3" type="checkbox" @click="showPassword()">{{ t('profile.showhide') }}
                </div>
                <div>
                        <button type="submit" class="btn-black px-3" v-html="t('profile.Login')"></button>
                </div>
        </form>
        <router-link to="/account">
                <label for="disabledTextInput" class="form-label b-16-gray" v-html="t('profile.newaccount')"></label>
        </router-link>
        <!-- Modal de error -->
        <div v-if="showModal" class="modal">
                <div class="modal-content">
                        <h3>{{ t('profile.messageError') }}</h3>
                        <br>
                        <button class="btn-black px-3" @click="showModal = false">{{ t('profile.btnCerrar') }}</button>
                </div>
        </div>
</template>

<script setup>
import { useI18n } from 'vue-i18n'
const { t } = useI18n()

import { useRouter } from 'vue-router'
import { ref } from 'vue'
import { API_BASE_URL } from '@/config/api'

const username = ref('')
const password = ref('')
const errorMessage = ref('')
const router = useRouter()
const showModal = ref(false)

const handleLogin = async () => {
        try {
                const response = await fetch(`${API_BASE_URL}/api/usuarios/Login`, {
                        method: 'POST',
                        headers: {
                                'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                                username: username.value,
                                password: password.value
                        })
                })

                const data = await response.json()
                console.log(data)
                if (data.status == 1) {
                        localStorage.setItem('token', data.data)

                        router.push('/') // Redirige al home
                } else {
                        console.log('Credenciales incorrectas')
                        errorMessage.value = response.message
                        showModal.value = true
                }
        } catch (error) {
                errorMessage.value = 'Error de conexión con el servidor'
                console.error(error)
        }
}

function showPassword() {
        var x = document.getElementById("pass");
        if (x.type === "password") {
                x.type = "text";
        } else {
                x.type = "password";
        }
}
</script>
<style scoped>
.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  padding:20px
}
.modal-content {
  background: white;
  padding: 40px;
  border-radius: 8px;
}
</style>
